import React, { useState } from 'react';
import { X, Bot, Sparkles, Lightbulb, Code } from 'lucide-react';

const AIPromptModal = ({ isOpen, onClose, onSubmit, workflow }) => {
  const [prompt, setPrompt] = useState('');
  const [context, setContext] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const examplePrompts = [
    "Add a Slack message node after the HTTP request that posts the result.",
    "Add a 10-second delay before step 1.",
    "If the response contains 'error', send an email and stop the workflow.",
    "Trigger the workflow with a webhook at /receive-order and forward the body to Notion.",
    "Add error handling to all HTTP requests.",
    "Create a condition that routes to different Slack channels based on the response status.",
    "Add a code node that transforms the data before sending to the next step.",
    "Schedule this workflow to run every day at 9 AM."
  ];

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!prompt.trim()) return;

    setIsLoading(true);
    try {
      await onSubmit(prompt, context);
      setPrompt('');
      setContext('');
      onClose();
    } catch (error) {
      console.error('AI update failed:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleExampleClick = (example) => {
    setPrompt(example);
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full mx-4 max-h-[90vh] overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <div className="flex items-center space-x-3">
            <div className="p-2 bg-purple-100 rounded-lg">
              <Bot className="h-6 w-6 text-purple-600" />
            </div>
            <div>
              <h2 className="text-xl font-semibold text-gray-900">AI Workflow Assistant</h2>
              <p className="text-sm text-gray-500">Describe what you want to change</p>
            </div>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="h-6 w-6" />
          </button>
        </div>

        {/* Content */}
        <div className="p-6 overflow-y-auto max-h-[calc(90vh-200px)]">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Current Workflow Info */}
            {workflow && (
              <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                <h3 className="text-sm font-medium text-blue-900 mb-2">
                  Current Workflow: {workflow.name}
                </h3>
                <div className="text-sm text-blue-700">
                  <p>Nodes: {workflow.nodes?.length || 0}</p>
                  <p>Edges: {workflow.edges?.length || 0}</p>
                </div>
              </div>
            )}

            {/* Prompt Input */}
            <div>
              <label htmlFor="prompt" className="block text-sm font-medium text-gray-700 mb-2">
                What would you like to change?
              </label>
              <textarea
                id="prompt"
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                placeholder="Describe the changes you want to make to your workflow..."
                className="w-full h-32 px-3 py-2 border border-gray-300 rounded-md focus:ring-purple-500 focus:border-purple-500 resize-none"
                required
              />
            </div>

            {/* Context Input */}
            <div>
              <label htmlFor="context" className="block text-sm font-medium text-gray-700 mb-2">
                Additional Context (Optional)
              </label>
              <textarea
                id="context"
                value={context}
                onChange={(e) => setContext(e.target.value)}
                placeholder="Any additional context, goals, or requirements..."
                className="w-full h-20 px-3 py-2 border border-gray-300 rounded-md focus:ring-purple-500 focus:border-purple-500 resize-none"
              />
            </div>

            {/* Example Prompts */}
            <div>
              <h3 className="text-sm font-medium text-gray-700 mb-3 flex items-center">
                <Lightbulb className="h-4 w-4 mr-2 text-yellow-500" />
                Example Prompts
              </h3>
              <div className="grid grid-cols-1 gap-2">
                {examplePrompts.map((example, index) => (
                  <button
                    key={index}
                    type="button"
                    onClick={() => handleExampleClick(example)}
                    className="text-left p-3 text-sm text-gray-600 bg-gray-50 hover:bg-gray-100 rounded-md transition-colors"
                  >
                    {example}
                  </button>
                ))}
              </div>
            </div>

            {/* Tips */}
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
              <h3 className="text-sm font-medium text-yellow-900 mb-2 flex items-center">
                <Sparkles className="h-4 w-4 mr-2" />
                Tips for Better Results
              </h3>
              <ul className="text-sm text-yellow-800 space-y-1">
                <li> Be specific about what you want to add, modify, or remove</li>
                <li> Mention the order or position of new nodes</li>
                <li> Include any conditions or logic requirements</li>
                <li> Specify API endpoints, channels, or data transformations</li>
              </ul>
            </div>
          </form>
        </div>

        {/* Footer */}
        <div className="flex items-center justify-end space-x-3 p-6 border-t border-gray-200 bg-gray-50">
          <button
            type="button"
            onClick={onClose}
            className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-purple-500"
          >
            Cancel
          </button>
          <button
            type="submit"
            onClick={handleSubmit}
            disabled={!prompt.trim() || isLoading}
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-purple-600 hover:bg-purple-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-purple-500 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoading ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Processing...
              </>
            ) : (
              <>
                <Bot className="h-4 w-4 mr-2" />
                Update Workflow
              </>
            )}
          </button>
        </div>
      </div>
    </div>
  );
};

export default AIPromptModal; 